.optional {
  color: green;
}

.toggle-switch {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
}

.switch {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 36px;
  height: 20px;
  margin: 0;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
  margin: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--inactive-color, #ccc);
  border: 1px solid var(--border-color, #aaa);
  transition: 0.4s;
  margin: 0;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 0px;
  top: 0px;
  background-color: var(--thumb-color, white);
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--active-color, var(--app-color-primary));
  border-color: var(--active-color, var(--app-color-primary));
}

input:focus + .slider {
  box-shadow: 0 0 0 2px var(--focus-color, var(--app-color-primary));
}

input:checked + .slider:before {
  transform: translateX(16px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* Disabled state */
input:disabled + .slider {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Error state */
.toggle-switch.error .slider {
  border: 1px solid var(--app-color-red);
}
